  grid-template-columns: repeat(2, 1fr);
  gap: 1.25rem 3.125rem;
  position: relative;
  border-top: 0.0625rem solid #DBDCDE;
}
.article-content .accordion__item:after {
  transform: translateY(-60%);
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-top: 0.1875rem solid transparent;
  border-bottom: 0.1875rem solid transparent;
  border-left: 0.375rem solid #DBDCDE;
}
.article-content .accordion__item:last-of-type {
  border-bottom: 0.0625rem solid #DBDCDE;
}
.article-content .accordion__item:last-of-type:before {
  transform: translateY(60%);
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  bottom: 0;
  border-top: 0.1875rem solid transparent;
  border-bottom: 0.1875rem solid transparent;
  border-left: 0.375rem solid #DBDCDE;
}
.article-content .accordion__content.active[style="max-height: 0px;"] {
  max-height: 100% !important;
}
.article-content .accordion__btn {
  display: grid;
  grid-template-columns: 1.875rem 1fr;
  gap: 1.875rem;
  color: #0F0F0F;
  font-family: Poppins;
  font-size: 26px;
  font-style: normal;
  font-weight: 300;
  line-height: 128%; /* 33.28px */
  position: relative;
  text-align: left;
  transition: all 300ms ease;
}
.article-content .accordion__btn:before {
  grid-row: 1;
  grid-column: 1;
  content: "";
  width: 100%;
  aspect-ratio: 1/1;
  background-color: rgba(var(--bg-d), 1);
  clip-path: var(--polly-button);
  top: 0;
  transition: opacity 300ms ease;
  background-image: var(--minus);
  background-repeat: no-repeat;
  background-position: center center;
}
.article-content .accordion__btn:after {
  position: absolute;
  grid-column: 1;
  grid-row: 1;
  content: "";
  left: 0.0625rem;
  top: 0.0625rem;
  grid-column: 1;
  width: 1.75rem;
  height: 1.75rem;
  background-color: rgba(var(--bg-a), 1);
  clip-path: var(--polly-button);
  background-image: var(--plus);
  background-repeat: no-repeat;
  background-position: center center;
  transition: opacity 300ms ease;
}
.article-content .accordion__btn.active {
  gap: 3.75rem;
}
.article-content .accordion__btn.active:after {
  opacity: 0;
}
.article-content .accordion__btn.active:before {
  background-color: rgba(var(--bg-f), 1);
}